*{padding:0;margin:0}
body,html{min-height:150%;width:100%}
body{font-family:'Segoe UI',Roboto,'Helvetica Neue',sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#000;}
h1{text-align:center}
a{display:inline-block;text-decoration:none;color:#000}
#container{display:flex;height:100%;flex-direction:column;width:100%}
#result-box{display:flex;justify-content:center;align-items:center;flex-direction:column}
.m-10{margin:10px !important}
.p-10{padding:10px !important}
.box-1{display:flex;justify-content:center;align-items:center;flex-flow: row wrap;}
.box-1>*{margin: 5px !important;text-align: center;}
[class*="flex-center"]{display: flex;justify-content: center;align-items: center;}
[class*="--wrap"]{flex-flow: row wrap;}
[class*="--column"]{flex-direction:column}
[class*="--stretch"]{align-items:stretch}
#repositories>*{margin:10px;padding: 10px;text-align: left;border: 1px solid #ddd;}
#repositories{width: 100%;}
#xddxd{display: block;}